.logos {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 790px;

    &__logo {

        width: 245px;
        height: 189px;
        border: 1px solid rgba(184, 184, 184, 0.5);
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 40px;
        flex-basis: 30%;
        transition: all .3s;
        color: $black;
        text-decoration: none;

        &:hover {
            border: 1px solid #6b6b6b80;

        }


    }

    &__title {
        font-size: 18px;
        margin-bottom: 20px;
        font-weight: 600;
    }

    &__image {
        margin-bottom: 25px;
    }

    &__ic {
        width: 32px;
        height: 30px;
    }

}

@media only screen and (max-width : 760px) {
    .logos {
        &__logo {
            flex-basis: 48%;
            margin-bottom: 10px;
        }
    }


}
