.blackboxes {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 30px;
}

.blackbox-wrp {
    text-align: center;
    transition: all .3s;
    border-radius: 5px;


    .filters-check {
        justify-content: center;
    }

    &:hover .blackbox {
        background: #3a3939;
    }
}

.blackbox {
    width: 245px;
    height: 243px;
    background: #202020;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-right: 30px;
    margin-bottom: 25px;
    transition: all .3s;
    border-radius: 5px;

    &__title {
        font-size: 16px;
        line-height: 32px;
        font-weight: 800;
        color: #fff;
        margin-bottom: 26px;
        text-align: center;
    }

    &__img {
        width: 37px;
        height: 27px;
    }


    &__ic {
        width: 32px;
        height: 30px;
        fill: $black;
        margin-right: 30px;


    }


}

@media only screen and (max-width : 610px) {
    .blackboxes {
        display: flex;
        justify-content: flex-start;
        margin-bottom: 30px;
        flex-wrap: wrap;
    }

    .blackbox-wrp {
        margin-bottom: 30px;
        flex-basis: 100%;
    }

    .blackbox {
        flex-basis: 100%;
        width: 100%;
        height: 243px;
        padding: 20px;

        &__ic {
            margin-right: 0px;


        }

    }


}
