.checkbox-hidden {
    display: none;
}

.filters-check {
    display: flex;
    margin: 0;
    cursor: pointer;
    font-size: 12px;
    font-weight: 600;
    align-items: center;

    &:not(:last-child) {
        margin-bottom: 15px;
    }

    .checkbox-wrap {
        background: url("../img/check1.svg") no-repeat 0 0;
        width: 20px;
        height: 20px;
        margin-right: 15px;

        &::after {
            content: '';
            display: block;
            width: 16px;
            height: 12px;
            background: url("../img/check2.svg") no-repeat 0 0;
            background-size: 100%;
            will-change: transform;
            transform: scale(0);
            opacity: 0;
            transition: all 0.1s linear;
            margin-left: 6px;
        }
    }

    .checkbox-text {
        display: inline-block;
        margin-top: 1px;
        margin-left: 14px;
        font-size: 14px;
    }

    input {
        &:checked {
            + {
                .checkbox-wrap {
                    &::after {

                        transform: scale(1);
                        opacity: 1;
                    }
                }
            }
        }
    }
}
