.breadcrumbs {
    margin-bottom: 10px;

    li {
        display: inline-block;

        a {
            color: $black;
            text-decoration: none;

            &:hover {
                color: gray;
            }
        }
    }

    li + li {
        &::before {
            color: $black;
            content: ">";
            padding: 0 5px;
        }

    }
}

.payment {
    width: 520px;



    &__maintext {
        font-family: "Raleway", sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 600;
        line-height: 24px;
        letter-spacing: 0em;
        text-align: left;
        color: #fff;
        padding: 17px 30px;
        background: #B8B8B8;
        border-left: 10px solid $yellow;
        margin-bottom: 20px;

    }
}

.input-group {
    margin: 25px 0;

    &__label {
        font-family: "Raleway", sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: 0.05em;
        text-align: left;
        margin-bottom: 10px;
        color: $black;
        text-transform: uppercase;
        display: block;
        width: 100%;
    }

    &__input,
    &__select,
    &__textarea {
        display: block;
        width: 100%;
        font-family: "Raleway", sans-serif;
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: 36px;
        letter-spacing: 0em;
        text-align: left;
        padding: 6px 20px;
        border: 1px solid #A3A4A6;
        background: #fff;
        box-sizing: border-box;
        border-radius: 5px !important;
        margin-bottom: 15px;
    }


    &__select {
        height: 48px;

        &:active,
        &:focus {
            outline: none;
        }
    }

    &__textarea {

        &:active,
        &:focus {
            outline: none;
        }


    }

    &__hint {
        font-family: "Raleway", sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 300;
        line-height: 24px;
        letter-spacing: 0.05em;
        text-align: left;
        color: $black;

    }
}


.blacktext {
    padding: 20px;
    background: $black;
    color: #fff;

    &__title {
        font-family: "Montserrat", sans-serif;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        letter-spacing: 0.05em;
        text-align: left;
        margin-bottom: 10px;

    }

    &__desc {
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px;
        letter-spacing: 0.05em;
        text-align: left;
    }

    a {
        color: $yellow;
        text-decoration: underline;
    }
}

@media only screen and (max-width : 600px) {

    .payment {
        width: 100%;
    }

    .input-group {
        margin: 18px 0;

        &__label {
            margin-bottom: 5px;
        }

        &__input,
        &__select {
            margin-bottom: 10px;
        }
    }
}
