.c-modal {
    position: absolute;
    visibility: visible;
    opacity: 0;
    transition: .3s;
    top: -1000px;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1000;
    background: rgba(32, 32, 32, 0.75);
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 60px 15px;

    &__btns {
        display: flex;
        justify-content: center;

    }

    &__title {
        font-size: 20px;
        font-style: normal;
        font-weight: 600;
        line-height: 40px;
        letter-spacing: 0em;
        text-align: center;
        margin-bottom: 40px;

    }

    &__btn {
        color: #fff;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 28px;
        letter-spacing: 0em;
        text-align: center;
        border-radius: 5px;
        background: $black;
        border: none;
        transition: all .3s;
        width: 100px;
        height: 40px;
        margin: 0 7px;

        &:hover {
            background: $yellow;
            color: $black;

        }

    }

}

.modal_active {
    position: fixed;
    top: 0;
    visibility: visible;
    opacity: 1;
    transition: .3s;
}

.c-modal__content {
    width: 100%;
    max-width: 520px;
    border-top: 11px solid $yellow;
    text-align: center;
    padding: 60px 80px;
    background: #f9f9f9;
    border-radius: 3px;
    position: relative;
    margin-left: 359px;
    margin-top: 100px;

}

@media only screen and (max-width : 1200px) {
    .c-modal__content {

        margin-left: 0px;


    }

}

@media only screen and (max-width : 562px) {
    .c-modal__content {
        padding: 60px 15px;
        margin: 100px 10px;

    }

    .c-modal {
        padding: 15px;

    }

}
